@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
.coin-container{
    display:flex;
    justify-content: center;
   }
   .coin-row{
     display:flex;
     flex-direction: row;
     justify-items: start;
     align-items: center;
     height: 80px;
     border-bottom: 1px solid #D16BA5;
     width: 900px;
   }
   
   .coin{
     display: flex;
     align-items: center;
     padding-right: 24px;
     min-width: 300px;
   }
   .coin h1{
     font-size: 16px;
     width: 150px;
   }
   
   .coin img{
     height: 30px;
     width: 30px;
     margin-right: 10px;
   }
   
   .coin-symbol{
     text-transform: uppercase;
   }
   .coin-data{
     display: flex;
     text-align: center;
     justify-content: space-between;
     width: 100%;
   
   }
   
   .coin-price{
     width: 110px;
   }
   
   .coin-volume{
     width: 200px;
   }
   
   .coin-marketcap{
     width:240px;
   
   }
   
   .coin-percent{
     width: 80px;
   }
   
   .red{
     color: #f00606;
   }
   
   .green{
     color: #11da11;
   }
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Cabin", sans-serif;
  background-color: #0c2d48;
  coloe: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #ffff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  color: #e576b5;
}

.coin-input {
  padding-left: 32px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    to right top,
    #d16ba5,
    #c777b9,
    #ba83ca,
    #aa8fd8,
    #9a9ae1,
    #8aa7ec,
    #79b3f4,
    #69bff8,
    #52cffe,
    #41dfff,
    #46eefa,
    #5ffbf1
  );
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

